import { getFileName } from "../../../../helpers/helper";
import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import { Empty, Collapse, Select, DatePicker, Row, Col, Divider } from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/report/purchaseProcessHistoryReport/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, customCollapseStyle } from "../../../App/app.style";
import PrintWrapper from "../../../App/PrintWrapper";
import ReactToPrint from "react-to-print";
import ButtonPrint from "../../../App/ButtonPrint";
import ExcelWrapper from "../../../App/ExcelWrapper";
import PDFWrapper from "../../../App/PDFWrapper";
import getLanguage from "../../../App/MyLanguange";
import { siteConfig } from "../../../../settings";

const Panel = Collapse.Panel;
const Option = Select.Option;
const { RangePicker } = DatePicker;

const redirectTo = "/dashboard/report-menus";
const redirectRQ = "/dashboard/inventories/item-requests";
const redirectPO = "/dashboard/purchases/purchase-orders";
const redirectPR = "/dashboard/purchases/purchase-receipts";
const redirectPI = "/dashboard/purchases/purchase-invoices";
const redirectPT = "/dashboard/purchases/purchase-returns";
const redirectPP = "/dashboard/financials/payable-payments";

class PurchaseProcessHistoryReport extends Component {
  state = {
    filter: {
      id_location: null,
      id_supplier: null,
      start_date: moment()
        .startOf("month")
        .format("YYYY-MM-DD HH:mm:ss"),
      end_date: moment().format("YYYY-MM-DD HH:mm:ss")
    }
  };

  // Get All data diawal
  componentDidMount() {
    const { getPurchaseProcessHistoryReports } = this.props;
    getPurchaseProcessHistoryReports();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.purchaseProcessHistoryReport.error) {
      notification("error", this.props.purchaseProcessHistoryReport.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.purchaseProcessHistoryReport.success) {
      notification("success", this.props.purchaseProcessHistoryReport.success);
      this.props.clearSuccess();
    }
  }

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  filterValue = () => {
    const { filter } = this.state;
    let string = "";

    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_location) {
      string
        ? (string += "&id_location=" + filter.id_location)
        : (string += "id_location=" + filter.id_location);
    }

    return string;
  };

  // Handle filter
  handleFilter = () => {
    const { getPurchaseProcessHistoryReports } = this.props;
    //const { current_page } = this.props.purchaseProcessHistoryReport;
    let filter = this.filterValue();

    getPurchaseProcessHistoryReports(1, null, filter, this.state.filter);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getPurchaseProcessHistoryReports } = this.props;
    this.setState({
      filter: {
        id_location: null,
        id_supplier: null,
        start_date: moment()
          .startOf("month")
          .format("YYYY-MM-DD HH:mm:ss"),
        end_date: moment().format("YYYY-MM-DD HH:mm:ss")
      }
    });
    getPurchaseProcessHistoryReports();
  };

  render() {
    // console.log(this.props);

    // Data setup
    const {
      purchase_process_history_reports,
      locations,
      suppliers,
      filter,
      filter_select
    } = this.props.purchaseProcessHistoryReport;
    //console.log(this.props.purchaseProcessHistoryReport.base_currency); //console.log(this.state);

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "sidebar.itemRequest" }),
        dataIndex: "item_request_no",
        rowKey: "item_request_no",
        width: "16.67%",
        render: (text, row) => {
          return text ? (
            <Link to={`${redirectRQ}/update/${row.id_item_request}`}>
              {text}
            </Link>
          ) : (
            "-"
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.purchaseOrders" }),
        dataIndex: "purchase_order_no",
        rowKey: "purchase_order_no",
        width: "16.67%",
        render: (text, row) => {
          return text ? (
            <Link to={`${redirectPO}/update/${row.id_purchase_order}`}>
              {text}
            </Link>
          ) : (
            "-"
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.purchaseReceipts" }),
        dataIndex: "purchase_receipt_no",
        rowKey: "purchase_receipt_no",
        width: "16.67%",
        render: (text, row) => {
          return text ? (
            <Link to={`${redirectPR}/update/${row.id_purchase_receipt}`}>
              {text}
            </Link>
          ) : (
            "-"
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.purchaseInvoices" }),
        dataIndex: "purchase_invoice_no",
        rowKey: "purchase_invoice_no",
        width: "16.67%",
        render: (text, row) => {
          return text ? (
            <Link to={`${redirectPI}/update/${row.id_purchase_invoice}`}>
              {text}
            </Link>
          ) : (
            "-"
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.purchaseReturns" }),
        dataIndex: "purchase_return_no",
        rowKey: "purchase_return_no",
        width: "16.67%",
        render: (text, row) => {
          return text ? (
            <Link to={`${redirectPT}/update/${row.id_purchase_return}`}>
              {text}
            </Link>
          ) : (
            "-"
          );
        }
      },
      {
        title: getLanguage({ id: "sidebar.payablePayment" }),
        dataIndex: "payable_payment_no",
        rowKey: "payable_payment_no",
        width: "16.67%",
        render: (text, row) => {
          return text ? (
            <Link to={`${redirectPP}/update/${row.id_payable_payment}`}>
              {text}
            </Link>
          ) : (
            "-"
          );
        }
      }
    ];

    const dataSource = [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    purchase_process_history_reports.map((data, key) => {
      dataSource.push({
        ...data,
        key
      });
    });

    const { loading, loadingForeign } = this.props.purchaseProcessHistoryReport;
    const fileName = getLanguage({
      id: "purchaseProcessHistoryReports.pageHeader"
    });

    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={
              <IntlMessages id="purchaseProcessHistoryReports.pageHeader" />
            }
            marginBottom={true}
          >
            <PrintWrapper
              componentRef={ref => (this.componentRef = ref)}
              title={
                <IntlMessages id="purchaseProcessHistoryReports.pageHeader" />
              }
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <ReactToPrint
              trigger={() => <ButtonPrint loading={loading} />}
              content={() => this.componentRef}
              onBeforePrint={() =>
                (document.title = getFileName(fileName, null, false))
              }
              onAfterPrint={() => (document.title = siteConfig.documentTitle)}
              pageStyle={
                "@page { size: landscape; }" +
                "body {" +
                "-webkit-print-color-adjust: exact;" +
                "} "
              }
            />

            <PDFWrapper
              columns={columns}
              data={dataSource}
              fileName={fileName}
              filter={filter_select}
              landscape={true}
              loading={loading}
              locations={locations}
              margin="1cm"
              scale={0.7}
              title="purchaseProcessHistoryReports.pageHeader"
            />

            <ExcelWrapper
              title="purchaseProcessHistoryReports.pageHeader"
              fileName={fileName}
              loading={loading}
              columns={columns}
              data={dataSource}
              filter={filter_select}
              locations={locations}
            />

            <Button
              style={{ marginLeft: "5px" }}
              onClick={() => this.props.history.replace(redirectTo)}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle} defaultActiveKey={["1"]}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="select.location" />}
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>

                  <Col span={12}>
                    <label>
                      <IntlMessages id="sidebar.supplier" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_supplier"
                      )}
                      value={
                        this.state.filter.id_supplier
                          ? this.state.filter.id_supplier
                          : []
                      }
                      style={{ width: "100%" }}
                      placeholder={
                        this.state.filter.id_location ? (
                          <IntlMessages id="select.supplier" />
                        ) : (
                          <IntlMessages id="noLocation" />
                        )
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.to_location.loadingFilterSites}
                      allowClear={true}
                      notFoundContent={
                        this.state.filter.id_location ? (
                          <Empty description={<IntlMessages id="noData" />} />
                        ) : (
                          <Empty
                            description={<IntlMessages id="noLocation" />}
                          />
                        )
                      }
                    >
                      {suppliers.map((data, key) =>
                        data.id_location === this.state.filter.id_location ? (
                          <Option key={key} value={data.id_supplier}>
                            {`${data.supplier_code} (${data.supplier_name})`}
                          </Option>
                        ) : null
                      )}
                    </Select>
                  </Col>
                </Row>
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="salesOrders.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={false}
                      style={{ width: "95%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>

                  <Col
                    span={12}
                    style={{
                      marginTop: "auto"
                    }}
                  >
                    <Button
                      size="small"
                      type="primary"
                      style={{ marginRight: "10px" }}
                      onClick={this.handleFilter}
                      disabled={!this.state.filter.id_location}
                    >
                      <IntlMessages id="filter.only" />
                    </Button>
                    <Button onClick={this.handleResetFilter} size="small">
                      <IntlMessages id="filter.reset" />
                    </Button>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {!filter ? (
              <Empty description={<IntlMessages id="noLocation" />} />
            ) : !filter.includes("id_location=") ? (
              <Empty description={<IntlMessages id="noLocation" />} />
            ) : purchase_process_history_reports.length === 0 &&
              !this.props.purchaseProcessHistoryReport.loading ? (
              <Empty
                description={
                  <IntlMessages id="purchaseProcessHistoryReports.table.noData" />
                }
              />
            ) : (
              <div className="isoInvoiceTable">
                <Divider orientation="left" style={{ fontSize: 14 }}>
                  <IntlMessages id="details" />
                </Divider>
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={false}
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    purchaseProcessHistoryReport: state.purchaseProcessHistoryReport,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(
  PurchaseProcessHistoryReport
);
